/* @flow */
'use strict'

import {
  take
} from 'redux-saga/effects'

export function* helloSaga(getState, action) {
  console.log('Hello Sagas!')
  while(true) {
    yield take('OPEN_NAVIGATION')
    yield console.log('Hello Sagas!')
  }
}

export function* rootSaga(): Generator<any, void, void> {
  // yield console.log('Hello Sagas!')
  yield [
   helloSaga(),
  //  watchIncrementAsync()
 ]
  // yield take('OPEN_NAVIGATION', logIt)
  // console.log('Hello Sagas!')
}



